<template>
  <div>
    <!-- {{this.empCode}} -->
    <b-table :small="true" :items="items" :fields="fields" :borderless="true">
      <template v-slot:cell(description)="row">
        <b-form-input
          class="col-left form-control form-control-sm"
          v-model="row.item.description"
        />
      </template>
      <template v-slot:cell(otherDeduction)="row">
        <b-form-input
          class="col-right form-control form-control-sm"
          type="number"
          v-model="row.item.otherDeduction"
        />
      </template>
      <template slot="bottom-row">
        <td class="row-background form-control form-control-sm">
          <h6>Total</h6>
        </td>
        <td class="col-right row-background">{{ this.t1.toFixed(3) }}</td>
      </template>
    </b-table>
    <button class="mr-1" variant="primary" @click="AddItemRow()">
      Add Row
    </button>
    <button class="mr-1" variant="danger" @click="RemoveItemRow()">
      Remove Row
    </button>
  </div>
</template>
<script>

import { mapActions, mapGetters } from 'vuex'
export default {
  props: {
    id: {},
    trnYear: {},
    trnMonth: {}
    // items: {}
  },
  data () {
    return {
      items: [{ description: '', otherDeduction: 0 }],
      fields: [
        {
          key: 'description',
          label: 'Description',
          class: 'text-left'
        },
        {
          key: 'otherDeduction',
          label: 'Other Deduction',
          class: 'text-right th.sm'
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['metDeduction']),
    t1: function () {
      return this.items.reduce(function (a, c) {
        return a + Number(c.otherDeduction || 0)
      }, 0)
    }
  },
  created () {
  },
  mounted () {
    this.items = this.metDeduction
  },
  methods: {
    ...mapActions(['addRowMetDeduction']),
    AddItemRow: function () {
      this.items.push({ description: '', otherDeduction: 0 })
    },
    RemoveItemRow: function () {
      this.items = [{}]
    }
  },
  watch: {
    t1: {
      handler: function () {
        this.$emit('deduction', this.items)
      }
    },
    id: {
      handler: function () {
        this.items = this.metDeduction
      }
    },
    items: {
      handler: function () {
        for (var i = 0; i < this.items.length; i++) {
          this.items[i].id = this.id
          this.items[i].trnYear = this.trnYear
          this.items[i].trnMonth = this.trnMonth
        }
        this.$emit('deduction', this.items)
      }
    }
  }
}
</script>
<style scoped>
.col-left {
  text-align: left
}
.col-right {
  text-align: right
}
.row-background {
  background-color: darkgray
}
</style>
